const state = {
    // 
    good: {
        shortDescription: [],
        shortDescriptionImage: []
    },
    // 
    comment: {},
    // 
    author: {},
    // 
    selection: {
        type: 0,
        num: 1
    }
}

const getters = {
  good: state => state.good,
  comment: state => state.comment,
  author: state => state.author,
  selection: state => state.selection
}

const mutations = {
    // 
    initGood(state, payload) {
        state.good = payload.good;
    },
    // 
    initComment(state, payload) {
        state.comment = payload.comment;
        state.author = payload.author;
    },
    // 
    changeNum(state, payload) {
        state.selection.num = payload.num;
    },
    // 
    changeType(state, payload) {
        state.selection.type = payload.type;
    },
    // 1
    addNum(state) {
        state.selection.num ++;
    },
    // 1
    subNum(state) {
        if (state.selection.num === 1) {
            return ;
        }
        else {
            state.selection.num --;
        }
    }
}

const actions = {

}

export default {
  state,
  getters,
  actions,
  mutations
}



